import './style.css'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { gsap } from 'gsap'
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js'
import { AxesHelper, PointLight, SpotLight, SpotLightHelper, SphereGeometry, MeshStandardMaterial, AmbientLight } from 'three'
import {TWEEN} from 'three/examples/jsm/libs/tween.module.min.js'
import { Interaction } from 'three.interaction';

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()

/**
 * Loaders
 */
const loadingBarElement = document.querySelector('.loading-bar')
const loadingPage = document.querySelector('.loading-page')
const loadingBody = document.getElementById('body')
const loadingManager = new THREE.LoadingManager(
    // Loaded
    () =>
    {
        window.setTimeout(() =>
        {
            gsap.to(overlayMaterial.uniforms.uAlpha, { duration: 3, value: 0, delay: 1 })

            loadingBarElement.classList.add('ended')
            loadingBarElement.style.transform = ''
            loadingPage.style.visibility = 'hidden'
            loadingBody.style.overflowY = 'scroll'
        }, 1500)
    },

    // Progress
    (itemUrl, itemsLoaded, itemsTotal) =>
    {
        const progressRatio = itemsLoaded / itemsTotal
        loadingBarElement.style.transform = `scaleX(${progressRatio})`
    }
)

/**
 * Overlay
 */
const overlayGeometry = new THREE.PlaneGeometry(2, 2, 1, 1)
const overlayMaterial = new THREE.ShaderMaterial({ 
    transparent: true,
    uniforms:
    {
        uAlpha: { value: 1 }
    },
    vertexShader: `
        void main()
        {
            gl_Position = vec4(position, 1.0);
        }
    `,
    fragmentShader: `
        uniform float uAlpha;
        void main()
        {
            gl_FragColor = vec4(0.0, 0.0, 0.0, uAlpha);
        }
    `
 })
const overlay = new THREE.Mesh(overlayGeometry, overlayMaterial)
scene.add(overlay)

/**
 * Lights
 */
 
const spotLight2 = new SpotLight('#958160', 1)
spotLight2.position.z = 0
spotLight2.position.x = -3
spotLight2.position.y = 3
spotLight2.angle = 60
spotLight2.power = 3
spotLight2.castShadow = true
scene.add(spotLight2)



/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight 
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height)
camera.position.set( -1, 1.3, 0)
camera.far = 20

// Controls
const controls = new OrbitControls(camera, canvas)
controls.target.set(0, 1, 0)
controls.enableDamping = true
controls.enableZoom = false
controls.enablePan = false
controls.enableRotate = true
controls.enabled = false

function updateCamera(ev) {
    camera.position.x = -1 - window.scrollY / 2500.0
    camera.rotation.z =  window.scrollY / 2500.0
}

window.addEventListener("scroll", updateCamera);

const dracoLoader = new DRACOLoader()
dracoLoader.setDecoderPath('/draco/')

const gltfLoader = new GLTFLoader(loadingManager)
gltfLoader.setDRACOLoader(dracoLoader)
gltfLoader.load(
    '/models/website.gltf',
    (gltf) =>
    {
        gltf.scene.scale.set(0.3, 0.3, 0.3)
        gltf.scene.position.y = -0.3
        gltf.scene.position.z = 0.4
        scene.add(gltf.scene)
        gltf.scene.receiveShadow = false; 

        var isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
        if (isMobile) {
            camera.far = 20
            camera.fov = 90
            controls.enabled = false
        }

        // const cube = gltf.scene.children[2]
        gltf.scene.traverse(function(o) {
            if (o.isMesh) {
              o.castShadow = true
              o.receiveShadow = true
            }
          }
        )
    }
)


/**
 * Renderer
 */

const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    alpha: true
})
renderer.shadowMap.enabled = true
spotLight2.shadow.bias = -0.0001;
spotLight2.shadow.mapSize.width = 1024*4;
spotLight2.shadow.mapSize.height = 1024*4;
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.gammaOutput = true 
renderer.shadowMap.type = THREE.PCFSoftShadowMap


/**
 * Animate
 */


const clock = new THREE.Clock()

const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()

    // Update controls
    controls.update()

    camera.updateProjectionMatrix()

    TWEEN.update()
    // Render
    renderer.render(scene, camera)
    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()

